<template>
  <div class="skin-display">
    <div id="model-container">
      <canvas id="c"></canvas>
    </div>
    <a class="dl-button" :href="this.skinUri" download="skin.png">Download Your Skin</a>
    <div class="flat-preview">
      <a :href="this.skinUri" download="skin.png">
        <img id="complete-skin" :src="this.skinUri" alt="Custom Minecraft Skin" />
      </a>
    </div>
  </div>
</template>

<style>

#model-container {
  width: 300px;
  height: 500px;
  margin: auto;
}

canvas {
  width: 100%;
  height: 100%;
  background: black;
  border-radius: 2px;
}

.dl-button {
  background: linear-gradient(#009147, #008542);
  color: white;
  text-decoration: none;
  display: inline-block;
  padding: 0.25rem 0.5rem;
  border: 4px solid;
  border-color: #27CE40 #0C6E3D #064D2A;
  margin: 0.25rem;
  font-weight: 600;
}

#complete-skin {
  width: 128px;
}
</style>

<script>
import mergeImages from 'merge-images';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';


/**
 * AAAAAAAHHH WHY MUST I HAVE A 3D PREVIEW
 */
export default {
  name: 'SkinDisplay',
  props: [
    "selected",
    "categories"
  ],
  computed: {
    selectedUrls: {
      get() {
        return this.selected.map((item, i) => this.categories[i]["items"][item]["skin"])}
    },
  },
  watch: {
    selectedUrls: function() {
      mergeImages(this.selectedUrls).then(b64 => this.skinUri = b64);
    },
    skinUri: function() {
      this.init(this.skinUri);
    }
  },
  data() {
    return {
      skinUri: 'data:image/png;base64,',
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      loader: null,
      skin: null,
      skin_opaque: null,
      skin_transparent: null,
      clock: null
    }
  },
  methods: {
    init: function(imageUri) {
      var vm = this;
        let container = document.getElementById('model-container');
        this.clock = new THREE.Clock();

        this.camera = new THREE.PerspectiveCamera(50, container.clientWidth/container.clientHeight, 0.01, 1000);
        this.camera.position.z = 45;

        this.scene = new THREE.Scene();

        this.skin = new THREE.TextureLoader().load(imageUri);
        this.skin.magFilter = THREE.NearestFilter;
        this.skin.flipY = false;

        this.skin_opaque = new THREE.MeshBasicMaterial({
            map: vm.skin,
            transparent: false,
            skinning: true,
        });

        this.skin_transparent = new THREE.MeshBasicMaterial({
           map: vm.skin,
            transparent: true,
            skinning: true,
        });


        this.loader = new GLTFLoader();

        var transparentMeshes = ["hat", "jacket", "leftSleeve", "rightSleeve", "leftPants", "rightPants"];

        this.loader.load('/assets/classic.glb',
        function(gltf) {
            gltf.scene.traverse(o => {

                if (!o.isMesh) console.log(o.name);

                if (o.isMesh) {
                    o.material = vm.skin_opaque;
                    if (transparentMeshes.indexOf(o.name) > -1) {
                        o.material = vm.skin_transparent;
                    }
                }
            });

            gltf.scene.position.y -= 16;
            gltf.scene.rotation.y += Math.PI/6;

            vm.mesh = gltf.scene;

            vm.scene.add(vm.mesh);
        },
        undefined, // We don't need this function
        function(error) {
            console.error(error);
        }
    );

        const canvas = document.querySelector('#c');
        this.renderer = new THREE.WebGLRenderer({canvas, antialias: true});
        this.renderer.setPixelRatio( window.devicePixelRatio );
        this.renderer.setSize(container.clientWidth, container.clientHeight);
        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        container.appendChild(this.renderer.domElement);

    },
    animate: function() {
      let vm = this;
        requestAnimationFrame(this.animate);
        // Walking: 4.317, Sprinting: 5.612, Sprint-jumping: 7.143
        const pace = 4.317;
        const tcos0 = (clock, walkSpeed) => (Math.cos((walkSpeed * clock) * 1.333) * walkSpeed / 6);
        this.mesh.traverse(o => {
          let time = vm.clock.getElapsedTime();
          if (o.name == "leftLeg") o.rotation.x = tcos0(time,pace)*1.4 + Math.PI/2;
          if (o.name == "rightLeg") o.rotation.x = -tcos0(time,pace)*1.4 + Math.PI/2;
          if (o.name == "leftArm") o.rotation.x = -tcos0(time,pace) + Math.PI;
          if (o.name == "rightArm") o.rotation.x = tcos0(time,pace) + Math.PI;
        });
        this.renderer.render(this.scene, this.camera);
    }
  },
  mounted() {
      this.init(this.skinUri);
      this.animate();
  }
}
</script>

