<template>
  <div class="category">
    <div class="category-header">{{ category.name }}</div>
    <div class="category-content">
      <div class="item-grid">
        <div v-for="(item, i) in category.items" :key="item.name">
          <input :id="category.name + '-' + i" class="item-input" type="radio" :name="category.name" :value="i" v-model="value" :checked="value" @change="$emit('input', $event.target.value)" />
          <div class="item">
            <label :for="category.name + '-' + i">
              <img :src="item.thumbnail || item.skin" />
              <div class="item-name">{{item.name}}</div>
            </label>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CategoryList',
  props: [
    "category",
    "value"
  ]
}
</script>

<style>
.category {
  background: #262626;
  color: #bebebe;
}

.category-header {
    display: flex;
    width: 100%;
    height: 48px;
    align-items: center;
    justify-content: center;
    background: #c6c6c6;
    color: #4c4c4c;
    font-size: 1.5rem;
    border: 4px solid;
    border-color: #f7f7f7 #656465 #656465 #f7f7f7;
    font-weight: 700;
}

.item-grid {
  display: flex;
  flex-wrap: wrap;
  padding: 0.5rem;
}

.item-grid > div {
  flex: 1 1 92px;
}

.item {
  margin: 0.5rem;
  background: #000;
  border: 2px solid;
  text-align: center;
  font-size: .8rem;
  cursor: pointer;
}

.item label {
  display: block;
  width: 100%;
}

.item img {
  width: 64px;
}

.item-input {
  display: none;
}

.item-input:checked + .item {
  color: white;
  font-weight: bold;
}
</style>
